import L from "leaflet";
import "leaflet/dist/leaflet.css";
import {
  Modal,
  Divider,
  Card,
  CardHeader,
  CardContent,
  CardActions,
  Paper,
  Button,
  IconButton,
  Stack,
  InputLabel,
  OutlinedInput,
  FormHelperText,
  Typography,
  Grid,
  Box,
  Select,
  MenuItem,
} from "@mui/material";
import { useState, useEffect, useRef } from "react";
import {
  MapContainer,
  TileLayer,
  Marker,
  Popup,
  Polyline,
  LayersControl,
  useMap,
} from "react-leaflet";

L.Icon.Default.imagePath = "https://unpkg.com/leaflet@1.6.0/dist/images/";
//console.log('L.Icon->',L.Icon.Default.imagePath+'/dist/image/');

const limeOptions = { color: "red" };

function Map(props) {
  const [polyline, setPolyline] = useState(props.polyline);
  const [center, setCenter] = useState(
    props.polyline[props.polyline.length - 1]
  );
  const [render, setRender] = useState(true);
  const [timer, setTimer] = useState(false);
  const [stop, setStop] = useState(false);
  const [map, setMap] = useState();
  const popupRef = useRef();
  var i = polyline.length;
  const timerId = useRef();
  useEffect(() => {
    if (timer) {
      timerId.current = setInterval(() => {
        var center = polyline[i - 1];
        console.log("center", center);
        setCenter(center);
        map.setView(center);
        i--;
        if (i == 0) {
          clearInterval(timerId.current);
          setTimer(false);
          console.log("타이머정지");
        }
      }, 1200);
    }
    if (stop) {
      console.log("정지");
      clearInterval(timerId.current);
      setStop(false);
    }
  }, [timer, stop]);

  function startTimer() {
    setTimer(true);
  }
  function stopTimer() {
    clearInterval(timerId.current);
    setTimer(false);
    //setStop(true);
    console.log(timerId.current);
  }

  function MapComponent() {
    const mapCon = useMap();
    // if (mapCon !== null) {
    //   mapCon.flyToBounds([[polyline[100]], [polyline[101]]]);
    // }
    // console.log('map center:', map.getCenter());
    return null;
  }

  return (
    <>
      <Paper
        sx={{
          minWidth: 300,
          minHeight: 500,
          maxHeight: 700,
          overflowY: "auto",
          p: 0,
        }}
      >
        <Card>
          <CardContent>
            <MapContainer
              style={{ minHeight: "500px" }}
              center={polyline[100]}
              zoom={7}
              scrollWheelZoom={true}
              whenCreated={(map) => {
                console.log("readyMap");
                setMap(map);
              }}
            >
              <MapComponent />
              <LayersControl position="topright">
                <LayersControl.BaseLayer
                  checked
                  name="OpenStreetMap.BlackAndWhite"
                >
                  <TileLayer
                    attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                    url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                  />
                </LayersControl.BaseLayer>
              </LayersControl>
              <Marker position={center}>
                <Popup ref={popupRef} closeButton={true}>
                  현재위치
                </Popup>
              </Marker>
              <Polyline pathOptions={limeOptions} positions={polyline} />
            </MapContainer>
          </CardContent>
          <Stack
            direction="row"
            justifyContent="center"
            alignItems="center"
            spacing={2}
          >
            <Button
              variant="contained"
              color="success"
              onClick={() => {
                map.setView(polyline[polyline.length - 1], 13);
                startTimer();
              }}
            >
              시작
            </Button>
            <Button variant="contained" color="success" onClick={stopTimer}>
              정지
            </Button>
          </Stack>
        </Card>
      </Paper>
    </>
  );
}

export default Map;
