//import 'leaflet/dist/leaflet.css';
import { useState, useEffect } from "react";
import Map from "./Map";
import {
  Modal,
  Divider,
  Card,
  CardHeader,
  CardContent,
  CardActions,
  Paper,
  Button,
  IconButton,
  Stack,
  InputLabel,
  OutlinedInput,
  FormHelperText,
  Typography,
  Grid,
  Box,
  Select,
  MenuItem,
} from "@mui/material";
import Track from "./Track/Track.json";

const polyline = Track;

const i = polyline.length;
const center = polyline[1];

function Main() {
  return (
    <Grid
      container
      justifyContent="space-around"
      alignItems="center"
      spacing={1}
    >
      <Grid item xs={12} md={12}>
        <h1>Map Test</h1>
      </Grid>
      <Grid item xs={12} md={12}>
        <Map polyline={polyline} center={center} />
      </Grid>
    </Grid>
  );
}

export default Main;
